<template>
	<div class="good-container">		
		<Row :gutter="10" class="overscroll">
			<Col span="8" v-for="item in pageSize" :key="item" class="mt-2">
				<Card dis-hover>
					<div class="good-item">
						<div class="good-img">
							<img src="../../img/good1.png">
						</div>
						<div class="good-content">
							<p class="title">考勤读卡器</p>
							<p class="desc">即插即用无需安装</p>
							<div class="clearfloat">
								<span class="money">￥200</span>
								<span class="pull-right icon-car cursor" @click="addCar">
									<Icon type="ios-cart-outline"></Icon>
								</span>
							</div>
						</div>
					</div>
				</Card>
			</Col>
		</Row>
		<div class="text-center mt-3">
			<Page :total="18" show-sizer :page-size="pageSize" @on-page-size-change="onPageSize" :page-size-opts="sizeOption"></Page>
		</div>
	</div>
</template>

<script>
	export default{
		data () {
			return {
				sizeOption: [6,12],
				pageSize: 6
			}
		},
		methods: {
			onPageSize (size) {
				this.pageSize = size
			},
			addCar () {
				
			},
		}
	}
</script>



